<template>
  <el-autocomplete v-model="scope.row.tire_un" @blur="clear" ref='autocomplete'  :fetch-suggestions="(queryString, callback)=>{querySearchAsync(queryString, callback,scope.row)}" placeholder="请输入内容" :disabled="scope.row.defaultTire" @select="select">
    <template slot-scope="{ item }">
      <ul>
        <li>{{ item.tire_un }}</li>
      </ul>
    </template>
  </el-autocomplete>
</template>

<script>
export default {
  props: ["scope", "id"],
  methods: {
    focus() {
      this.$refs.autocomplete.focus();
    },
    querySearchAsync(queryString, callback, row) {
      this.$set(row, "is_select", 0);
      this.$axMethod
        .getUnUseTireList({
          tire_goods_id: this.id,
          tire_un: queryString
        })
        .then(res => {
          if (res.data.code == "200") {
            callback(res.data.data);
          }
        });
    },
    select(val) {
      this.$emit("select", val);
    }
  }
};
</script>

