    <div class="add_delet">
      <el-button icon="el-icon-circle-plus-outline" type="primary" @click="addF" class="addBtn" size="mini">增行</el-button>
      <el-button plain icon="el-icon-delete" @click="deletes" size="mini">删行</el-button>
      <el-button plain icon="el-icon-download" @click="upload" size="mini">下载模板</el-button>
      <el-tooltip class="item" effect="dark" content="批量导入文件格式请按照文件模板格式，详情请点击【下载文件模板】，查看模板文件" placement="top-start">
