          </template>
        </el-table-column>
      </el-table> -->

      <xTable ref='xTable' :tableColumn='tableColumn' :xData='TableData.list' :isEdit='isEdit' />
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="goBack">取 消</el-button>
      <el-button type="primary" @click="submit">确 定</el-button>
    </div>
    <upload :options="options" @receiveUploadMsg="receiveUploadMsg" ref="upload"></upload>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import { tableHead } from "./config/tiremanagementAdd.js";
export default {
  name: "instorageTiremanagementTiremanagementAdd",
  mixins: [tableHead],
  data() {
    return {
      aaa: 11,
      options: {
        title: "胎号导入",
        url: "/v2/inventory/Tire/importTire"
      },
      authority: {},
      TableData: {
        list: []
      },
      // params: {},
      searchParams: {}, //参数传递值
      infoParams: {}, //
      goPage: 1, //默认搜索页面
      pageSize: 30, //默认表格size
      loading: true,
      per_page: [30, 50, 100, 500],
      multipleSelection: [],
      defaultTableLen: 0
    };
  },
  computed: {
    ...mapGetters(["tiremanagementIndexGet", "tiremanagementAddGet"])
  },
  methods: {
    ...mapActions(["tiremanagementIndexACT", "tiremanagementSeeACT"]),
    dialogEmployee() {
      this.$refs.upload.stateOpen();
    },
    receiveUploadMsg(response) {
      this.initData();
      this.goBack();
    },
    scanCode() {
      this.$message({
        message: "请使用扫码枪进行信息录入",
        type: "success"
      });
    },
    goBack() {
      this.$router.push({
        path: "/inventory/tiremanagement"
      });
      this.tiremanagementIndexACT({
        params: this.tiremanagementAddGet.params,
        type: this.tiremanagementAddGet.row.type
      });
    },
    handleSelect(item, row) {
      Object.assign(row, item, { is_select: item.tire_id });
    },
    goSee() {
      this.$router.push({
        path: "/inventory/tiremanagement/tiremanagementSee"
      });
      this.tiremanagementSeeACT({
        row: this.tiremanagementAddGet.row,
        params: this.tiremanagementAddGet.params,
        type: this.tiremanagementAddGet.row.type
      });
    },
    upload() {
      this.$axMethod.tireDdownloadFileUrl({}).then(response => {
        if (response.data.code == 200) {
          window.location.href = response.data.data.url;
        }
      });
    },
    handleCurrentChange(val) {
      this.params.page = val;
      this.initData();
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    addF() {
      this.TableData.list.push({
        tire_un: "",
        tire_sn: "",
        remark: ""
      });
    },
    deletes() {
      let status = true;
      if (this.multipleSelection.length) {
        this.multipleSelection.forEach(itme => {
          if (itme.tire_id) {
            status = false;
          }
        });
        if (!status) {
          this.$message({
            message: "非新增胎号，请到查看界面删行！",
            type: "warning"
          });
          return false;
        }
        let a = new Set(this.TableData.list);
        let b = new Set(this.multipleSelection);

        let differenceABSet = new Set([...a].filter(x => !b.has(x)));
        this.$set(this.TableData, "list", Array.from(differenceABSet));
      } else {
        this.$message({
          message: "请先选择要删除的商品！",
          type: "warning"
        });
      }
    },
    initData() {
      this.$axMethod.tireGetTireList(this.infoParams).then(({ data }) => {
        if (data.code == 200) {
          data.data.data.forEach(obj => {
            obj.defaultTire = true;
          });
          this.defaultTableLen = data.data.data.length;
          this.TableData.list = data.data.data;
          this.addF();
        }
      });
    },

    submit() {
      let newParams = {};
      let flag = true;
      let submitTable = this.TableData.list.filter(
        item => item.defaultTire != true
      );
      if (!submitTable.length) {
        this.$message.warning("请完善胎号信息");
        return false;
      }
      let tire_json = submitTable.map(item => {
        if (!item.tire_un) {
          this.$message.warning("请完善胎号信息");
          flag = false;
          return false;
        }
        return {
          tire_un: item.tire_un,
          tire_sn: item.tire_sn ? item.tire_sn : "",
          is_select: item.is_select,
          remark: item.remark ? item.remark : ""
        };
      });
      if (!flag) return false;
      Object.assign(
        newParams,
        {
          tire_goods_id: this.tiremanagementAddGet.row.tire_goods_id,
          type: this.tiremanagementAddGet.row.type
        },
        {
          tire_json: JSON.stringify(tire_json)
        }
      );
      this.$axMethod.addTire(newParams).then(res => {
        if (res.data.code == 200) {
          this.$message({
            message: res.data.msg,
            type: "success"
          });
          this.goSee();
        }
      });
    },
    createdActive() {
      this.params = {
        type: this.tiremanagementAddGet.row.type,
        json_info: this.tiremanagementAddGet.row,
        goods_id: this.tiremanagementAddGet.row.goods_id,
        real_num: this.tiremanagementAddGet.row.real_num
      };
      this.options.params = {
        type: this.tiremanagementAddGet.row.type,
        tire_goods_id: this.tiremanagementAddGet.row.tire_goods_id,
        goods_id: this.tiremanagementAddGet.row.goods_id
      };
      this.infoParams = {
        tire_goods_id: this.tiremanagementAddGet.row.tire_goods_id
      };
      this.initData();
    }
  },
  activated() {
    window.vxeKeyupCallBack = this.goNextLine;
  },
  created() {
    this.createdActive();
    window.vxeKeyupCallBack = this.goNextLine;
  }
  /*
      由于使用{{params.json_info.unit_name}}格式 故添加数据只能在dom渲染前
      缓存页面created只触发一次 所以同样代码写两遍
    */
};
</script> 
